
html body #root{
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  letter-spacing: 3px;
}
.navlink {
  color : white; 
  font-size: 24px;
  margin-right: 8px;
}

.navlink:hover {
  color: rgb(77, 79, 224);
  font-size: 30px;
}

.open {
  color: rgb(91, 91, 173);
  font-size: 24px;
  margin-right: 8px;
}

.open:hover {
  color: rgb(241, 176, 176);
  font-size: 24px;
  margin-right: 8px;
}

.nav {
  font-family: revenant
}
.foot{
    color: gray;
    font-size: 12px;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    margin-left: 2px;
    padding-bottom: 8px;
}
.title {
    color: white;
    font-size: 80px;
}

.subtitle{
    color: white;
    font-size: 20px;
}

.head {
    color: white;
    font-size: 40px;
    letter-spacing: 10px;
    margin-left: 40px;
    margin-right: 70px;
    text-orientation: sideways;
    max-height: 600px;
}

.slide {
    width: 100%;
}

.slides {
    max-width: 75%;
}

.codes {
    text-align: left;
}
.blue {
    background-color: rgb(138, 193, 224);
}

.red {
