  }
.slides {
    max-width: 80%;
}

.topicbtn{
    margin: 2%;
    width: 130px;
    background-color: rgb(216, 111, 111);
    border: 3px solid rgb(38, 11, 49);
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    border-radius: 20px;
}

.topicbtn:hover {
    background-color: transparent;
    border: 3px solid rgb(216, 111, 111);
}

.opn{
    margin: 2%;
    width: 130px;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    border-radius: 20px;
    background-color: transparent;
    border: 3px solid rgb(216, 111, 111);
}


.opn:hover{
    margin: 2%;
    width: 130px;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    border-radius: 20px;
    background-color: transparent;
    border: 3px solid rgb(216, 111, 111);
}
.pc{
    width: 100%;
    margin-right:0;
